@import (less) 'color.less';

.sign-info-header-container {
  padding-top: 20px;
  padding-bottom: 20px;
}

.base-section {
  min-height: 120px;
  position: relative;
}

.sign-type-section {
  .base-section;
  background-color: @white;
  border: 1px solid @light_gray;
}

.sign-customer-info-section {
  .base-section;
  background-color: @dirt_gray;
  border-top: 1px solid @light_gray;
  border-bottom: 1px solid @light_gray;
  border-right: 1px solid @light_gray;
  border-left: 0px solid transparent;
  padding-top: 10px;
  &:before {
    content: '';
    display:block;
    width:20px;
    height:20px;  
    background-color: @dirt_gray;
    border-top: 1px solid @light_gray;
    border-left: 1px solid @light_gray;
    position:absolute;
    transform-origin:top left;
    transform:rotate(-45deg) translateX(-12px) translateY(-50%);
    top:50%;
    left:0px;    
  }
  
}

@media screen and (max-width: 767px) {
  .sign-customer-info-section  {
    border-top: 0px solid transparent;
    border-left: 1px solid @light_gray !important;
    &:before {
      border-top: 1px solid @light_gray;
      border-right: 1px solid @light_gray;
      border-left: 0px solid transparent;
      top: -15px;
      left: 34px;
    }
  } 
 
}

.sign-info-text {
  padding: 0 0 0 100px;
  font-size: 25px;
  top: 50%;
  transform: translateY(-50%);   
  position: absolute;
  color: @green;
}

.sign-info-icon {
  position: absolute;
  font-size: 60px;
  top: -37px;
  left: 50px;
  background-color: inherit;
  padding: 0 10px 0 10px;
}

.customer-info-container {
  padding-left: 50px;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}

.customer-info-label {
  font-weight: bold;
}

.customer-info-text {
  padding-top: 6px;
}